import React from 'react';
import ReactDom from 'react-dom';
import Dropdown from '../index';
import Icon from '../../icon/index';
import Button from '../../button/index';

const DropdownNestDemo = function () {
  let options = [
    {
      name: 'java',
      value: 101,
      element: <div>
        <span
          style={{ float: 'left' }}
        >bottom</span>
        <Icon
          type="arrow-bottom"
          style={{ float: 'right' }}
        />
      </div>
    }, {
      name: 'swift',
      value: 121,
      element: <div>
        <Button>Button</Button>
      </div>,
    }, {
      name: 'js',
      value: 332,
      element: <strong style={{ color: 'red' }}>你好</strong>,
    }
  ];
  return (
    <Dropdown
      options={options}
      style={{ width: '150px' }}
    />
  );
};

ReactDom.render(<DropdownNestDemo />, document.getElementById('nest'));
